import React from "react"
import Link from "gatsby-link"
import styled from "styled-components"
import Helmet from "react-helmet"
import Fade from "react-reveal/Fade"
import {
  TINY_SHADOW,
  TINY_BKG,
  SECONDARY_TEXT
} from "../../components/consts/colors.js"

import "../../css/index.css"
import "../../css/typography.css"
import "../../css/Hide.css"
import SectionContainer from "../../components/SectionContainer"
import SectionContainerBackground from "../../components/SectionContainerBackground"
import SectionHeaderContent from "../../components/SectionHeaderContent"
import SectionContent from "../../components/SectionContent"
import {
  TinyButton,
  TinyButtonAlt,
  TinyButtonWhite
} from "../../components/Button.js"
import SectionImages from "../../components/SectionImages"
import SectionImage from "../../components/SectionImage"
import FAQHeader from "../../components/FAQHeader"
import fathom from "../../assets/Fathom.png"

import withScreensize from "../../components/withScreensize"
import ScreensizeProvider from "../../components/ScreensizeProvider"

const pageData = {
  FAQHeadline: "Ready to get Started?",
  FAQCopy:
    "What do you have to lose? TinyMerch is completely free, you can have it up and running on your site in just a few minutes.",
  FAQLink: "http://my.tinymerch.com",
  FAQwhatIsCTA: "Get Started!"
}

const HowToStart = withScreensize(props => {
  const isMobile = props.screensize === "phones"
  const isTablet = props.screensize === "thatUglyInBetweenSize"
  const isSmall = props.screensize === "devWithConsoleOpen"
  const isWidescreen = props.screensize === "widescreenFools"

  return (
    <div>
      <Helmet title="TinyMerch | FAQ – What is TinyMerch" />
      <FAQHeader />
      <SectionContainer positionContent="center">
        <SectionContent
          justify="center"
          color
          height={
            isMobile ? (
              "300px"
            ) : isTablet ? (
              "400px"
            ) : isWidescreen ? (
              "500px"
            ) : (
              "500px"
            )
          }
          width={
            isMobile ? "95%" : isTablet ? "80%" : isWidescreen ? "40%" : "50%"
          }>
          <Fade left delay={50} duration={400}>
            <h2>{pageData.FAQHeadline}</h2>
          </Fade>
          <Fade left delay={100} duration={400}>
            <p style={{ paddingTop: "25px" }}>{pageData.FAQCopy}</p>
          </Fade>
          <Fade left delay={150} duration={400}>
            {/* <NextQ>
              <Link to="/faq/how-it-works">
                🤔 So How does it work?
                <Icon name="forward" color="#6079F2" />
              </Link>
            </NextQ> */}
            <a href={pageData.FAQLink}>
              <TinyButton>{pageData.FAQwhatIsCTA}</TinyButton>
            </a>
          </Fade>
        </SectionContent>
        <SectionImages
          width={
            isMobile ? "100%" : isTablet ? "100%" : isWidescreen ? "60%" : "50%"
          }
          height={
            isMobile ? (
              "200px"
            ) : isTablet ? (
              "300px"
            ) : isWidescreen ? (
              "600px"
            ) : (
              "600px"
            )
          }>
          <Fade right delay={350} duration={500}>
            <SectionImage
              shadow
              src={fathom}
              ImgWidth={
                isMobile ? (
                  "300px"
                ) : isTablet ? (
                  "500px"
                ) : isWidescreen ? (
                  "700px"
                ) : (
                  "600px"
                )
              }
              positionTB={
                isMobile ? (
                  "10px"
                ) : isTablet ? (
                  "50px"
                ) : isWidescreen ? (
                  "80px"
                ) : (
                  "100px"
                )
              }
              positionLR={
                isMobile ? "0%" : isTablet ? "0%" : isWidescreen ? "20%" : "10%"
              }
            />
          </Fade>
        </SectionImages>
      </SectionContainer>
    </div>
  )
})

export default props => (
  <ScreensizeProvider>
    <HowToStart {...props} />
  </ScreensizeProvider>
)



